.footer {
  width: 100vw;
  height: 96px;
  box-shadow: 0 -1px 0 0 $light-gray;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 152px;

  &__logo {
    margin: 36px 79px 36px 0;
  }

  &__links {
    display: flex;
    gap: 64px;
    margin: 43px 0;
  }

  &__link {
    color: $gray;
    font-family: Mont, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 11px;
    letter-spacing: 0.48px;
    text-transform: uppercase;
    text-decoration: none;
  }

  &__top {
    &-nav {
      display: flex;
      gap: 16px;
    }

    &-link {
      width: 32px;
      height: 32px;
      border: 1px solid $middle-gray;
      margin-top: 32px;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        border: 1px solid $gray-close-black;
      }

      &-text {
        color: $gray;
        text-align: right;
        font-family: Mont, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-top: 41px;
        text-decoration: none;
      }

      &-image {
        width: 16px;
        height: 16px;
      }
    }
  }
}
