.slider {
  display: flex;
  gap: 16px;
  width: 100%;
  justify-content: center;

  &__nav {
    background-color: white;
    width: 32px;
    height: 400px;
    border: 1px solid $middle-gray;
  }

  &__arrow {
    width: 16px;
    height: 16px;
  }

  &__image {
    width: 1040px;
    height: 400px;
  }
}
